import Button from '@/app/component/Button';
import jobs from '@/app/data/jobs.js'
export default function JobCategory({params}) {
  const filteredJobs = jobs.filter(job => job.category.replace(/\s+/g, '-').toLowerCase() === params.cat);
  return (
    <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4'>
     
     {
        filteredJobs.map((job) => (
            <section key={job.id} className="p-4 m-4 bg-gray-100 rounded-lg shadow-md border border-gray-700">
                <h2 className="text-4xl font-bold ">{job.title}</h2>
                <p className="text-lg text-slate-700 border-2 border-gray-700"><span className="font-bold">Company:</span> {job.company}</p>
                <p className="text-lg text-slate-700 border-2 border-gray-700"><span className="font-bold">Location:</span> {job.location}</p>
                <p className="text-lg text-slate-700 border-2 border-gray-700"><span className="font-bold">Category:</span> {job.category}</p>
                <p className="text-lg text-slate-700 border-2 border-gray-700"><span className="font-bold">Description:</span> {job.description}</p>
                <p className="text-lg text-slate-700 border-2 border-gray-700"><span className="font-bold">End Date:</span> {new Date(job.endDate).toLocaleDateString()}</p>
                <p className="text-lg text-slate-700 border-2 border-gray-700"><span className="font-bold">Posted On:</span> {new Date(job.postedOn).toLocaleDateString()}</p>
                <p className="text-lg text-slate-700 border-2 border-gray-700"><span className="font-bold">Salary:</span> 500k per month {job.salary}</p>
                <p className="text-lg text-slate-700 border-2 border-gray-700"><span className="font-bold">Experience:</span> 2years {job.experience}</p>
                <p className="text-lg text-slate-700 border-2 border-gray-700"><span className="font-bold">Requirements:</span> {job.requirements.join(', ')}</p> <Button  title={job.title} />

            </section>

     ))}
    </div>
  )
}
